import React, {Component} from "react";

const ProductListPriceFilterRowView = (props) => {

        return (
            <li className="u-spacing opl-gradient-fade--item" key={"key_"+props.type}>
                <label className={"o-"+props.type+" opl-"+props.type+" u-block"}>
                    <input type={props.type}  checked={props.checked ? true : false} onClick={props.onClick}/>
                    <span className="o-ci"></span>
                    <span className="o-ci-label u-font-normal">{props.text}</span>
                </label>
            </li>
       )
}

export default ProductListPriceFilterRowView;